"use client";


import { useParams, useRouter } from "next/navigation";
import { formatDollar } from "@/shared/utils/currency.util";
import { ArrowLeftIcon } from "@/assets/svg/svg_icon";

interface OrderItem {
    id: number;
    status: string;
    statusColor: string;
    date: string;
    productName: string;
    quantity: number;
    price: number;
    image?: string;
}

const OrderDetailsPage = () => {
    const params = useParams();
    const router = useRouter();
    const orderId = params.id as string;

    const orderInfo = {
        orderId: `#${orderId}`,
        paymentMethod: "Paypal",
        subtotal: 650.5,
        shippingFee: 50.5,
        total: 700.5,
    };

    const billingAddress = {
        name: "Otor John Stephen",
        street: "38 Asheik Jarma Street, Jabi Abuja",
        email: "myemailaddress@gmail.com",
        phone: "08130000000",
    };

    const shippingAddress = {
        name: "Otor John Stephen",
        street: "38 Asheik Jarma Street, Jabi Abuja",
        email: "myemailaddress@gmail.com",
        phone: "08130000000",
    };

    const orderItems: OrderItem[] = [
        {
            id: 1,
            status: "Processing",
            statusColor: "bg-[#0095FF]",
            date: "Tuesday 16th Dec, 2025",
            productName: "The name of the product",
            quantity: 2,
            price: 600.0,
        },
        {
            id: 2,
            status: "Shipped",
            statusColor: "bg-[#E26E00]",
            date: "Tuesday 16th Dec, 2025",
            productName: "The name of the product",
            quantity: 2,
            price: 600.0,
        },
    ];

    return (
        <div className="bg-white rounded-[20px] border border-gray-200">
            {/* Header */}
            <div className="mb-6 border-b border-gray-200 pb-4 p-5">
                <button
                    onClick={() => router.back()}
                    className="flex items-center gap-2 text-gray-900 transition-colors cursor-pointer"
                >
                    <ArrowLeftIcon />
                    <span className="text-2xl font-semibold">Order Details</span>
                </button>
                <p className="text-gray-800">
                    View the details of your order and track order
                </p>
            </div>

            {/* Address Cards and Order Summary */}
            <div className="grid grid-cols-1 lg:grid-cols-3 gap-4 mb-8 border-b border-gray-200 pb-8 px-5">
                {/* Billing + Shipping Wrapper */}
                <div className="lg:col-span-2 bg-white border border-gray-200 rounded-[20px] overflow-hidden">
                    <div className="grid grid-cols-1 md:grid-cols-2">
                        {/* Billing Address */}
                        <div className="border-b md:border-b-0 md:border-r border-gray-200">
                            <h3 className="font-semibold text-gray-900 mb-4 border-b border-gray-200 pb-2 px-4 py-4">
                                Billing Address
                            </h3>

                            <div className="space-y-3 text-sm p-4">
                                <div>
                                    <p className="font-medium text-gray-900">
                                        {billingAddress.name}
                                    </p>
                                    <p className="text-gray-600">
                                        {billingAddress.street}
                                    </p>
                                </div>

                                <div>
                                    <p className="text-gray-500 text-xs mb-1">Email</p>
                                    <p className="text-gray-900">
                                        {billingAddress.email}
                                    </p>
                                </div>

                                <div>
                                    <p className="text-gray-500 text-xs mb-1">Phone</p>
                                    <p className="text-gray-900">
                                        {billingAddress.phone}
                                    </p>
                                </div>
                            </div>
                        </div>

                        {/* Shipping Address */}
                        <div className="">
                            <h3 className="font-semibold text-gray-900 mb-4 border-b border-gray-200 pb-2 px-4 py-4">
                                Shipping Address
                            </h3>

                            <div className="space-y-3 text-sm p-5">
                                <div>
                                    <p className="font-medium text-gray-900">
                                        {shippingAddress.name}
                                    </p>
                                    <p className="text-gray-600">
                                        {shippingAddress.street}
                                    </p>
                                </div>

                                <div>
                                    <p className="text-gray-500 text-xs mb-1">Email</p>
                                    <p className="text-gray-900">
                                        {shippingAddress.email}
                                    </p>
                                </div>

                                <div>
                                    <p className="text-gray-500 text-xs mb-1">Phone</p>
                                    <p className="text-gray-900">
                                        {shippingAddress.phone}
                                    </p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                {/* Order Summary */}
                <div className="bg-white border border-gray-200 rounded-[20px]">
                    <div className="">
                        <div className="flex justify-between border-b border-gray-200 pb-2 px-5 py-4">
                            <div className="flex flex-col">
                                <span className="text-gray-600">Order ID</span>
                                <span className="font-medium text-gray-900">
                                    {orderInfo.orderId}
                                </span>
                            </div>

                            <div className="h-10 w-[1px] bg-gray-300" />

                            <div className="flex flex-col">
                                <span className="text-gray-500">Payment method</span>
                                <span className="font-medium text-gray-900">
                                    {orderInfo.paymentMethod}
                                </span>
                            </div>
                        </div>

                        <div className="p-5 space-y-2">
                            <div className="flex justify-between">
                                <span className="text-gray-600">Subtotal:</span>
                                <span className="font-medium text-gray-900">
                                    {formatDollar(orderInfo.subtotal)}
                                </span>
                            </div>

                            <div className="flex justify-between">
                                <span className="text-gray-600">Shipping fee:</span>
                                <span className="font-medium text-gray-900">
                                    {formatDollar(orderInfo.shippingFee)}
                                </span>
                            </div>

                            <div className="flex justify-between">
                                <span className="font-semibold text-gray-900">Total:</span>
                                <span className="gradient-text text-lg text-medium">
                                    {formatDollar(orderInfo.total)}
                                </span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            {/* Items in Your Order */}
            <div className="px-5 py-4">
                <div className="bg-white border border-gray-200 rounded-[20px] p-6">
                    <h3 className="text-xl font-semibold text-gray-900 mb-6">
                        Items in your order
                    </h3>

                    <div className="space-y-6">
                        {orderItems.map((item) => (
                            <div key={item.id}>
                                {/* Status Badge */}
                                <div className="flex items-center gap-3 mb-3">
                                    <span
                                        className={`${item.statusColor} text-white px-4 py-1.5 rounded-[8px]`}
                                    >
                                        Status: {item.status}
                                    </span>
                                </div>

                                {/* Date */}
                                <p className="text-gray-600 mb-4">On {item.date}</p>

                                {/* Product Details */}
                                <div className="flex items-center justify-between pb-6 border-b border-gray-200 last:border-b-0 last:pb-0">
                                    <div className="flex items-center gap-4">
                                        <div className="w-20 h-20 bg-gray-200 rounded-lg flex-shrink-0"></div>
                                        <div>
                                            <p className="font-medium text-gray-900 mb-1">
                                                {item.productName}
                                            </p>
                                            <p className="text-sm text-gray-600 mb-1">
                                                QTY: {item.quantity}
                                            </p>
                                            <p className="text-base text-gray-900">
                                                {formatDollar(item.price)}
                                            </p>
                                        </div>
                                    </div>

                                    {/* Action Buttons */}
                                    <div className="flex gap-4">
                                        <button
                                            type="button"
                                            className="group w-48 p-0.5 border border-[#003625] rounded-[18px] cursor-pointer disabled:opacity-50 disabled:cursor-not-allowed">
                                            <div className="w-full bg-[#003625] text-white py-4 rounded-[14px] text-center font-semibold transition-colors duration-200 group-hover:bg-emerald-100 group-hover:text-[#052214]">
                                                Cancel Order
                                            </div>
                                        </button>
                                        <button
                                            className="group w-48 p-0.5 border border-[#FDA106] rounded-[18px] cursor-pointer"
                                        >
                                            <div className="relative w-full rounded-[14px] overflow-hidden bg-gradient">
                                                {/* Hover Overlay */}
                                                <div className="absolute inset-0 bg-[#FFF4EA] opacity-0 transition-opacity duration-300 group-hover:opacity-100 flex items-center justify-center">
                                                    <span className="text-[#FDA106] font-semibold">
                                                        See Status History
                                                    </span>
                                                </div>

                                                {/* Default State */}
                                                <div className="relative z-10 py-4 font-semibold flex items-center justify-center text-white transition-opacity duration-300 group-hover:opacity-0">
                                                    See Status History
                                                </div>
                                            </div>
                                        </button>
                                    </div>
                                </div>
                            </div>
                        ))}
                    </div>
                </div>
            </div>
        </div>
    );
};

export default OrderDetailsPage;